import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import ko from "date-fns/locale/ko";
import { forwardRef, useState } from "react";
import "../style/datePickerCustom.css";
import { path } from "./requestUrl";
import { useDispatch } from "react-redux";
import {
  inputCurrentTime,
  inputDepDate,
  inputDepTime,
  inputNewDate,
  inputNxtday,
  inputToday,
} from "../../store/getDateSlice";

const DatePickerCustom = () => {
  const dispatch = useDispatch();

  const [startDate, setStartDate] = useState(new Date());
  const ExampleCustomInput = forwardRef(({ value, onClick }, ref) => (
    <img
      src={`${path}/images/ico_calender.png`}
      alt=" "
      onClick={onClick}
      ref={ref}
    />
  ));

  //  /   
  const setDateandTime = (now) => {
    const year = now.getFullYear();
    const month = ("0" + (now.getMonth() + 1)).slice(0, 2);
    const day = now.getDate() < 10 ? "0" + now.getDate() : now.getDate();

    // API URL   ex. 20230214
    const today = `${year}${month}${day}`;
    dispatch(inputDepDate(today));

    // / /        .   ex. 202302140036
    // 1        -1
    let prevHour = now.getHours();
    let currentHour = now.getHours();
    const minutes = now.getMinutes();
    if (prevHour > 2) {
      prevHour -= 1;
    }
    const min = minutes < 10 ? "0" + minutes : minutes;
    const prevTime = `${prevHour < 10 ? "0" + prevHour : prevHour}${min}`;
    const currentTime = `${
      currentHour < 10 ? "0" + currentHour : currentHour
    }${min}`;
    const currentDepTime = `${today}${currentTime}` * 1;
    const depTime = `${today}${prevTime}` * 1;
    dispatch(inputCurrentTime(currentDepTime));

    dispatch(inputDepTime(depTime));

    //  
    const getWeek = now.getDay();
    const week = ["", "", "", "", "", "", ""];

    // ... 
    const nowDay =
      year + ". " + month.slice(1, 2) + ". " + day + ". " + week[getWeek];
    const nxtDay =
      year +
      ". " +
      month.slice(1, 2) +
      ". " +
      (day * 1 + 1) +
      ". " +
      week[getWeek !== 6 ? getWeek + 1 : 0];

    dispatch(inputToday(nowDay));
    dispatch(inputNxtday(nxtDay));
    dispatch(inputNewDate(JSON.stringify(new Date())));
  };

  return (
    <DatePicker
      selected={startDate}
      onChange={(date) => {
        setStartDate(date);
        setDateandTime(date);
      }}
      locale={ko}
      minDate={new Date()}
      customInput={<ExampleCustomInput />}
    />
  );
};

export default DatePickerCustom;
